@keyframes gradient {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}

.wave {
  background: rgba(4, 2, 147, 0.567);
  border-radius: 1000% 1000% 0 0;
  position: fixed;
  width: 200%;
  height: 12em;
  animation: wave 12s ease-in-out infinite;
  transform: translate3d(0, 0, 0);
  opacity: 0.7;
  bottom: 0;
  left: 0;
  z-index: -1;
  will-change: transform;
}

.wave:nth-of-type(2) {
  bottom: -1.5em;
  animation: wave 16s ease-in-out reverse infinite;
  opacity: 0.6;
}

.wave:nth-of-type(3) {
  bottom: -3em;
  animation: wave 20s ease-in-out reverse infinite;
  opacity: 0.5;
}

@keyframes wave {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-25%);
  }
  50% {
    transform: translateX(-50%);
  }
  75% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(0);
  }
}
