/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Contoh penggunaan container */
.container {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
}

/* Media Query untuk layar lebih besar (Tablet & Desktop) */
@media (min-width: 768px) {
  body {
    padding: 20px;
  }

  .container {
    max-width: 1024px;
  }

  a {
    color: blue;
  }
}
